<template>
	<view>
		<view class="page">
            <view class="utilizationView">
                <view class="utilizationView_circle">
                    <ch-circle-progress :percent="80" activeColor="#34ab15" bgColor="transparent" borderWidth="20"
                        width="250">
                        <view class="utilizationView_circle_content">
                            <view class="utilizationView_circle_content_precent">80 <text>%</text></view>
                            <view style="font-size: 22upx">今天利用率</view>
                        </view>
                    </ch-circle-progress>
                </view>
                <view class="utilizationView_rate">
                    <view class="utilizationView_rate_day">
                        <text class="ut-text">昨日设备利用率:</text>
                        <view>
                            <ch-line-progress :height="40" :progress="utilizationYestorday" line-color="#00c486" bg-color="#969a99">
                                <view slot="linetips" class="linetips">{{utilizationYestorday}}%</view>
                            </ch-line-progress>
                        </view>
                    </view>
                    <view class="utilizationView_rate_day">
                        <text  class="ut-text">近7日设备利用率:</text>
                        <view>
                            <ch-line-progress :height="40" :progress="utilizationSevenday" line-color="#00c486" bg-color="#969a99">
                                <view slot="linetips" class="linetips">{{utilizationSevenday}}%</view>
                            </ch-line-progress>
                        </view>
                    </view>
                </view>
            </view>

            <view class="today-plan bgwhite mt20">
                <view class="title-wrap"><ch-title title="今日计划进度" weight></ch-title></view>
                <view class="ch-flex plan-wrap">
                    <view class="ch-flex-1">
                        <view class="plan-wrap-num">2500</view>
                        <text class="plan-wrap-name">当前完成量</text>
                    </view>
                    <view class="ch-flex-1">
                        <view class="plan-wrap-num">25000</view>
                        <text class="plan-wrap-name">今日总量</text>
                    </view>
                </view>
                <view class="line-wrap">
                    <u-line-progress :percentage="2500/25000*100" showText height="20"></u-line-progress>
                </view>
            </view>

            <view class="reportList bgwhite mt20">
                <view class="title-wrap">
                    <ch-title title="报表管理" weight>
                        <view slot="right">
                            <u-icon @click="displayType=1" name="list-dot" v-if="displayType === 2" size="20" color="#333"></u-icon>
                            <u-icon @click="displayType=2" name="grid" v-if="displayType===1" size="20" color="#333"></u-icon>
                        </view>
                    </ch-title>
                </view>
                <view class="report-wrap">
                    <template v-if="displayType === 1">
                        <u-cell-group>
                            <u-cell v-for="(item, index) in list" :key="index" :title="item.name">
                                <view slot="icon"><u-icon :name="item.icon" size="22"></u-icon></view>
                            </u-cell>
                        </u-cell-group>
                    </template>
                    <template v-if="displayType === 2">
                        <u-grid :col="2" border class="gridmenu">
                            <u-grid-item v-for="(item, index) in list" :key="index">
                                <view class="ch-flex" >
                                    <view class="icon"><u-icon :name="item.icon" size="38"></u-icon></view>
                                    <div class="ch-flex-1">{{item.name}}</div>
                                </view>
                            </u-grid-item>
                        </u-grid>
                    </template>
                </view>
            </view>

		</view>
	</view>
</template>

<script>

export default {
	data() {
		return {
            displayType: 1,
			list: [
                { icon: 'photo', name: '次品报表', route: "/pages/report/bad" },
                { icon: 'photo', name: '次品报表', route: "/pages/report/bad" },
                { icon: 'photo', name: '次品报表', route: "/pages/report/bad" },
                { icon: 'photo', name: '次品报表', route: "/pages/report/bad" },
                { icon: 'photo', name: '次品报表', route: "/pages/report/bad" },
                { icon: 'photo', name: '次品报表', route: "/pages/report/bad" },
			],
			utilizationYestorday: 30,
			utilizationSevenday: 50,
			changeSlot: false
		};
	},
	onNavigationBarButtonTap({ index }) {
		if (index === 0) {
			//  扫码
			this.$u.toast('扫码')
		}
		if (index === 1) {
			// 搜索
			this.$u.toast('搜索')
		}
	},
	methods: {
	},
}
</script>

<style lang="scss" scoped>
//.scrollView {
//	height: calc(100vh - calc(var(--status-bar-height) + 0.64rem));
//}

.collapseItem {
	background-color: white;
	height: auto;
}

.utilizationView {
	width: 100%;
	height: 300upx;
	display: flex;
	flex-direction: row;
	margin-bottom: 10px;
	background-color: white;
	align-items: center;

	&_circle {
		width: 50%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;

		&_content {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;

			&_precent {
				font-size: 30px;
				font-weight: bold;
				color: black;
			}
		}
	}

	&_rate {
		width: 50%;
		display: flex;
		flex-direction: column;
		padding-left: 20px;
		padding-right: 20px;

		&_day {
			width: 100%;
			height: 50%;
			display: flex;
			flex-direction: column;
			justify-content: center;
            margin-bottom: 20upx;

			text {
				margin-bottom: 10px;
			}
		}
	}
}

.progressView {
	width: 100%;
	height: 200upx;
	display: flex;
	flex-direction: column;

	&_num {
		width: 100%;
		height: 50%;
		display: flex;
		flex-direction: row;
		align-items: center;

		&_content {
			width: 100%;
			height: 100%;
			display: flex;
			flex-direction: column;
			color: white;
			text-align: center;
			justify-content: center;

			text {
				padding: 2px;
				color: #909399;
			}
		}

		&_line {
			width: 1px;
			height: 30%;
			background-color: #909399;
		}
	}

	&_pro {
		width: 100%;
		height: 50%;
		display: flex;
		align-items: center;
	}
}

.grid-text {
	font-size: 14px;
	color: #909399;
	padding: 10rpx 0 20rpx 0rpx;
	/* #ifndef APP-PLUS */
	box-sizing: border-box;
	/* #endif */
}

.ut-text{
    font-size: 28upx;
    color: #666;
}
.linetips{
    position: relative;
    z-index: 100;
    text-align: center;
    color: #fff;
    font-size: 26upx;
    line-height: 42upx;
}
.title-wrap{
    padding: 24upx ;
}
.today-plan{
    .line-wrap{
        padding: 24upx 60upx;
    }
}
.plan-wrap{
    align-items: center;
    justify-content: center;
    text-align: center;
    &>.ch-flex-1{
        padding: 16upx 0;
        &:first-child{
            position: relative;
            &:after{
                position: absolute;
                content: "";
                width: 1px;
                height: 50%;
                background-color: #f2f2f2;
                top: 25%;
                right: 0;
            }
        }
    }
    &-num{
        font-size: 38upx;
        font-weight: 600;
        margin-bottom: 10upx;
    }
    &-name{
        font-size: 26upx;
        color: #666;
    }

}
.gridmenu{
    .ch-flex{
        padding: 20upx 0;
        align-items: center;
        .ch-flex-1{
            font-size: 28upx;
            color: #333;
            margin-left: 10upx;
        }
    }
}
</style>

